














































.ant-card-loading .ant-card-body p {
    word-break: break-all;
    line-height: 10px;
    margin: 5px 0 0;
    height: 10px;
    border-radius: 6px;
    overflow: hidden;
    display: inline-block;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: #f3f5f8;
}



